.section {
	padding: 50px 0;

	@include tablet {
		padding: 80px 0;
	}

	&-progress-bar {
		&__content {
			padding: 10px 0;

			@include desktops {
				padding: 15px 0;
			}
		}
	}

	&-welcome-for-quiz {
		background-image:
			url('../image/bg-shapes/wave-top.svg'),
			url('../image/bg-shapes/wave-bottom.svg');
		background-repeat: no-repeat;
		background-position: top, bottom;
		background-size: 100vw 150px, 100vw 250px;
		padding: 50px 0;
		overflow: hidden;

		@include tablet {
			padding: 80px 0 100px;
		}

		@include desktops {
			padding: 120px 0 200px;
		}

		&__text-block {
			text-align: center;
			margin-bottom: 50px;
		}

		&__subtitle {
			color: $primary;
			font-weight: 700;
		}

		&__description {
			max-width: 800px;
			margin: 0 auto;
			font-weight: 300;
		}
	}

	&-compare {
		@include desktops {
			background-position: left center;
			background-repeat: no-repeat;
			background-size: auto;
			background-image: url('../image/bg-shapes/round-shape-left.png');
		}

		[class^="col-md"] {
			&:not(:last-child) {
				margin-bottom: 40px;

				@include tablet {
					margin-bottom: 0;
				}
			}
		}
	}

	&-welcome {
		&__description {
			margin-bottom: 32px;
		}

		&__content {
			padding-top: 40px;

			@include mobile {
				padding-top: 50px;
			}

			@include desktops {
				padding-top: 0;
			}
		}

		&__mailchimp-bottom-text {
			padding: 32px 0 0 0;
			font-size: 16px;
			color: #666666;
			font-weight: 400;

			@include mobile {
				padding: 5px 0 0 20px;
			}
		}
	}

	&-with-logos {
		padding: 55px 0;

		&__title {
			font-weight: 600 !important;
			line-height: 1.25 !important;
			max-width: 560px;
			margin-left: auto;
			margin-right: auto;
			text-align: center;
			font-size: 24px !important;
			margin-bottom: 20px !important;

			@include mobile-lg {
				font-size: 32px !important;
			}
		}

		&__logo {
			display: block;
			width: auto;
			max-width: 120px;
			min-width: 100px;
			max-height: 30px;
			margin: 15px 0;
		}
	}

	&-with-video-and-cards {
		padding: 40px 0;
		counter-reset: card-step;

		@include mobile-lg {
			padding: 60px 0 30px;
		}

		&__title-holder {
			text-align: center;
			margin-bottom: 60px;

			@include mobile-lg {
				margin-bottom: 80px;
			}
		}

		&__video-block {
			position: relative;
			max-width: 600px;
			margin: 0 -24px 85px;
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center;

			@include mobile-lg {
				border-radius: 40px;
				margin: 0 auto 110px;
			}
		}

		&__video-block-inner {
			padding-bottom: 56.667%;
		}

		&__video-play-btn {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			display: inline-block;
			width: 47px;
			height: 47px;
			border-radius: 50%;
			background-color: $black;
			transition: background-color 0.3s;

			&:after {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-35%, -50%);
				display: block;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 7.5px 0 7.5px 13px;
				border-color: transparent transparent transparent $white;
			}

			&:hover {
				background-color: $primary;
			}
		}

		&__cards-block {
			@include mobile-lg {
				padding: 0 50px;
			}

			.row {
				[class^="col"] {
					&:last-child {
						.card-step {
							@media(max-width: $screen-md - 1px) {
								margin-bottom: 0;
							}
						}
					}
				}
			}
		}
	}

	&-title-cards-and-button {
		padding: 40px 0;

		@include mobile-lg {
			padding: 60px 0;
		}
	}

	&-repeating-blocks {
		padding: 40px 0;

		@include mobile-lg {
			padding: 60px 0;
		}

		&__list {
			> *:last-child {
				margin-bottom: 0;
			}

			> :nth-child(2n) {
				.row {
					flex-direction: column-reverse;

					@include tablet {
						flex-direction: row-reverse;
					}
				}
			}
		}

		&__block-item {
			margin-bottom: 60px;

			@include mobile-lg {
				margin-bottom: 100px;
			}

			.row {
				@media(max-width: $screen-md - 1px) {
					flex-direction: column-reverse;
				}
			}
		}

		&__block-item-text-content {
			margin-top: 32px;

			@include tablet {
				margin-top: 0;
			};
		}
	}

	&-reviews {
		padding: 40px 0;

		@include mobile-lg {
			padding: 60px 0;
		}
	}

	&-sign-up {
		padding: 40px 0;

		@include mobile-lg {
			padding: 60px 0;
		}

		&__title-with-link {
			display: flex;
			flex-direction: column;
			font-size: 32px !important;
			margin-bottom: 40px;

			@include mobile-lg {
				display: block;
				font-size: 52px !important;
			}

			@include desktops {
				font-size: 67px !important;
			}

			a {
				margin-top: 15px;

				@include mobile-lg {
					margin-left: 24px;
					vertical-align: middle;
					margin-top: 0;
				}
			}
		}

		&__small-title {
			max-width: 600px;
		}

		&__description {
			a {
				color: $black;
				font-weight: 700;

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}

.home-page {
	font-size: 16px;
	line-height: 1.5;
	font-weight: 500;
	color: $black;

	@include mobile-lg {
		font-size: 18px;
		line-height: 2;
	}

	h1, .h1,
	h2, .h2,
	h3, .h3,
	h4, .h4,
	h5, .h5,
	h6, .h6 {
		font-weight: 900;
		color: inherit;

		strong {
			color: $primary;
		}
	}

	h1, .h1 {
		font-size: 50px;
		line-height: 1;
		letter-spacing: -0.01em;
		margin-bottom: 16px;

		@include mobile-lg {
			font-size: 67px;
			margin-bottom: 48px;
		}
	}

	h2, .h2 {
		font-size: 40px;
		margin-bottom: 48px;

		@include mobile-lg {
			font-size: 52px;
		}
	}

	h3, .h3 {
		margin-bottom: 24px;
		line-height: 1.18;
		font-size: 32px;

		@include mobile-lg {
			margin-bottom: 32px;
			font-size: 38px;
		}
	}

	h4, .h4 {
		font-size: 24px;
		line-height: 1.32;
		letter-spacing: -0.32px;
		margin-bottom: 32px;

		@include mobile-lg {
			font-size: 32px;
		}
	}

	h5, .h5 {
		margin-bottom: 24px;
		font-size: 20px;
		line-height: 1.333;

		@include mobile-lg {
			font-size: 24px;
		}
	}

	p {
		color: inherit;
		font-size: inherit;
	}

	.section-title-holder {
		max-width: 695px;
		margin: 0 auto 60px;
		text-align: center;

		@include mobile-lg {
			margin-bottom: 80px;
		}
	}

	.small-section-title {
		font-weight: 600;
		line-height: 1;
		text-transform: uppercase;
		color: #666666;
		font-size: 14px;
		margin-bottom: 24px;

		@include mobile-lg {
			font-size: 16px;
		}
	}

	.section-description {
		font-size: 24px;
		font-weight: 500;
		line-height: 1.5;
	}
}