.progress-bar {
	position: relative;
	overflow: hidden;
	height: 35px;
	background-color: var(--bg-3);
	border-radius: 44px;
	padding: 5px;

	@include desktops {
		height: 44px;
		padding: 8px;
	}

	&__inner {
		position: absolute;
		top: 5px;
		left: 5px;
		bottom: 5px;
		max-width: calc(100% - 10px);
		width: 100%;
		background-color: $blue-3;
		border-radius: 44px;
		animation: progress-bar-stripes 2s linear infinite;
		background-image: linear-gradient(-45deg, lighten($blue-3, 10%) 25%, transparent 25%, transparent 50%, lighten($blue-3, 10%) 50%, lighten($blue-3, 10%) 75%, transparent 75%, transparent);
		background-size: 40px 40px;
		transition: width 0.3s;

		@include desktops {
			top: 8px;
			left: 8px;
			bottom: 8px;
			max-width: calc(100% - 16px);
		}
	}
}

@keyframes progress-bar-stripes {
	from {background-position: 0 0;}
	to {background-position: 40px 0;}
}

.progress-bar-block {
	@include mobile-lg {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}

	&__progress-bar {
		flex-grow: 1;
		margin-bottom: 5px;

		@include mobile-lg {
			padding-right: 15px;
			margin-bottom: 0;
		}

		@include tablet {
			padding: 0 15px;
		}

		@include desktops {
			padding: 0 30px;
		}
	}

	&__left-title, &__time {
		margin-bottom: 0;
		font-weight: 300;
		font-size: 18px;

		@include desktops {
			font-size: 24px;
		}
	}

	&__left-title {
		width: 100%;
		text-align: center;
		margin-bottom: 10px;
		text-transform: uppercase;

		@include tablet {
			width: auto;
			text-align: initial;
			margin-bottom: 0;
		}
	}

	&__time {
		min-width: 85px;
		flex-shrink: 1;
		text-align: center;

		@include mobile-lg {
			text-align: initial;
		}
	}
}