.footer {
	overflow: hidden;
	padding-top: 50px;

	&__main-content {
		position: relative;
		padding: 80px 0;

		@include desktops {
			padding-top: 120px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 2px;
			background-image: linear-gradient(to right, $black 10%, rgba($white, 0) 0%);
			background-position: center top;
			background-size: 10px 1px;
			background-repeat: repeat-x;
		}

		&:after {
			content: '';
			display: block;
			height: 8px;
			width: 100vw;
			position: absolute;
			bottom: 0;
			left: 0;
			background-color: $primary;
		}
	}

	&__logo {
		display: inline-block;
		max-width: 150px;
		margin-top: 5px;
		margin-bottom: 50px;
	}

	&__nav-item {
		margin-bottom: 40px;

		h5 {
			font-weight: 900;
			font-size: 20px;
			letter-spacing: -0.01em;
			margin-bottom: 20px;

			@include mobile-lg {
				font-size: 24px;
			}
		}

		ul {
			li {
				&:not(:last-child) {
					margin-bottom: 16px;
				}

				a {
					font-size: 16px;
					color: $black;
					line-height: 2;

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}

	&__socials-block {
		padding-top: 80px;
	}

	&__bottom-info-block {
		padding-top: 100px;
		font-size: 12px;
		color: $black;

		@include desktops {
			padding-top: 190px;
		}

		p {
			font-size: inherit;
			color: inherit;
		}
	}
}

.footer-additional-nav-list {
	display: flex;
	flex-wrap: wrap;
	font-size: 12px;

	li {
		&:not(:last-child) {
			&:after {
				content: '/';
				display: inline;
				padding: 0 5px;
			}
		}
	}

	a {
		color: $black;

		&:hover {
			text-decoration: underline;
		}
	}
}