.quiz-form {

	&:not([novalidate="novalidate"]) {
		display: none;
	}

	input[type='text'],
	input[type='tel'],
	input[type='email'],
	input[type='search'],
	input[type='password'],
	input[type='url'],
	select,
	textarea {
		@include placeholder {
			color: #6f706f;
			opacity: 1;
			transition: opacity 0.3s;
		}

		display: block;
		width: 100%;
		border: 2px solid $gray-400;
		border-radius: 40px;
		background-color: #f2f2f2;
		padding: 10px;
		font-size: 16px;
		font-weight: 300;
		line-height: 22px;
		color: $black;
		transition: border-color 0.3s;

		@include desktops {
			padding: 16px;
			font-size: 18px;
		}

		&:focus {
			outline: none;
			border-color: darken($gray-400, 15%);

			@include placeholder {
				opacity: 0;
			}
		}

		&.error {
			border-color: $red;
		}

		&.name-input {
			background-image: url("../image/avatar.png");
			background-repeat: no-repeat;
			background-position: 8px center;
			background-size: 30px;
			padding-left: 45px;

			@include desktops {
				padding-left: 60px;
				background-size: 39px;
			}
		}

		&.email-input {
			background-image: url("../image/email.png");
			background-repeat: no-repeat;
			background-position: 10px center;
			background-size: 25px;
			padding-left: 45px;

			@include desktops {
				padding-left: 60px;
				background-position: 15px center;
				background-size: 32px;
			}
		}
	}

	input[type="submit"] {
		color: #fff;
		background-color: $blue-3;
		border-color: $blue-3;
		border-radius: 40px;
		border: none;
		transition: color 0.3s, background-color 0.3s;
		font-size: 16px;
		padding: 9px 10px;
		letter-spacing: 1px;

		&:hover {
			color: #fff;
			background-color: darken($blue-3, 5%);
			border-color: darken($blue-3, 5%);
		}

		@include desktops {
			font-size: 20px;
			padding: 11px 15px;
		}
	}

	textarea {
		border-radius: 5px;
		height: 250px;
		resize: none;
	}

	// top navigation
	.steps {
		ul[role="tablist"] {
			list-style-type: none;
			padding-left: 0;
			margin-bottom: 0;
			display: flex;
			justify-content: center;

			.current-info {
				display: none !important;
			}

			li {
				position: relative;
				flex-basis: 85px;
				flex-grow: 1;
				min-width: 0;
				max-width: 85px;

				&.disabled {
					a {
						cursor: default !important;
					}
				}

				&:not(:last-child) {
					&:after {
						content: '';
						display: block;
						height: 2px;
						background-color: $gray-400;
						position: absolute;
						top: 50%;
						left: 50%;
						width: 100%;
						transform: translate(0, -50%);
					}
				}

				&.error {
					a {
						background-color: $red !important;
					}
				}

				&.done {
					a {
						background-color: lighten($blue-3, 15%);
						transition: color 0.3s, background-color 0.3s;

						&:hover {
							background-color: darken($blue-3, 5%);
						}
					}
				}

				&.current {
					a {
						cursor: default;
						background-color: $blue-3;
					}
				}
			}

			a {
				width: 16px;
				height: 16px;
				display: flex;
				align-items: center;
				justify-content: center;

				@include brk-point(400px) {
					width: 18px;
					height: 18px;
				}

				@include tablet {
					width: 22px;
					height: 22px;
				}

				position: relative;
				z-index: 5;
				border-radius: 50%;
				margin: 0 auto;
				background-color: $gray-400;
				color: $white;
				transition: color 0.3s, background-color 0.3s;
			}

			.number {
				font-size: 10px;
			}
		}
	}

	// content
	.content {
		padding: 50px 0;

		.title {
			display: none;
		}

		.step-section {
			max-width: 880px;
			margin: 0 auto;

			.step-title {
				text-align: center;
				text-transform: uppercase;
				margin-bottom: 20px;
			}

			.fieldset {
				display: flex;
				justify-content: center;
				flex-wrap: wrap;

				input[type='text'],
				input[type='tel'],
				input[type='email'],
				input[type='search'],
				input[type='password'],
				input[type='url'],
				textarea {
					flex-basis: 100%;

					@include mobile-lg {
						margin-left: 7px;
						margin-right: 7px;
						width: calc(100% - 14px);
					}

					&:not(:last-child) {
						margin-bottom: 10px;

						@include mobile-lg {
							margin-bottom: 14px;
						}
					}
				}

				.standart-checkbox,
				.standart-radio {
					margin: 0 0 10px;

					@include mobile-lg {
						flex: 0 0 50%;
						max-width: 50%;
						padding: 0 7px;
						margin: 0 0 14px;
					}
				}

				.checkbox-with-icon,
				.radio-with-icon {
					margin: 0 0 10px;

					@include mobile-lg {
						flex: 0 0 50%;
						max-width: 50%;
						padding: 0 7px;
						margin: 0 0 14px;
					}

					@include tablet {
						flex: 0 0 25%;
						max-width: 25%;
					}
				}

				.irs--flat {
					margin: 0 7px 14px;
					flex-basis: 100%;
				}

				&.error {
					.standart-checkbox,
					.standart-radio,
					.checkbox-with-icon,
					.radio-with-icon {
						&__item {
							background-color: $red;
						}
					}
				}
			}

			label.error {
				display: block;
				color: $red;
				text-align: center;
				font-size: 16px;
				margin-bottom: 0;
			}
		}
	}

	// bottom buttons
	.actions {
		ul[role="menu"] {
			list-style-type: none;
			padding-left: 0;
			margin-bottom: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;

			li {
				margin: 5px;

				&.disabled {
					a {
						background-color: $gray-400;
						border-color: $gray-400;
						cursor: default !important;

						&:active {
							background-color: $gray-400 !important;
							border-color: $gray-400 !important;
						}
					}
				}
			}

			a {
				@extend .btn;
				@extend .btn-2;

				color: #fff;
				background-color: $blue-3;
				border-color: $blue-3;
				border-radius: 5px !important;
				min-width: 150px;
				transition: 0.4s cubic-bezier(0.39, 0.575, 0.565, 1);

				&:hover {
					color: #fff;
					background-color: darken($blue-3, 5%);
					border-color: darken($blue-3, 5%);
				}
			}
		}
	}
}

.standart-checkbox,
.standart-radio {
	position: relative;
	display: block;
	width: 100%;
	margin-bottom: 0;

	&:hover {
		.standart-checkbox__item,
		.standart-radio__item {
			background-color: darken($blue-3, 5%);
		}
	}

	&__input {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		padding: 0;
		border: 0;
		clip: rect(0 0 0 0);
		overflow: hidden;

		&:checked {
			+ .standart-checkbox__item,
			+ .standart-radio__item {
				background-color: darken($blue-3, 20%);

				.standart-checkbox__icon,
				.standart-radio__icon {
					border-color: $white;

					&:after {
						opacity: 1;
					}
				}
			}
		}

		&.error {
			+ .standart-checkbox__item,
			+ .standart-radio__item {
				background-color: $red;
			}
		}
	}

	&__item {
		position: relative;
		padding: 10px 10px 10px 40px;
		font-size: 14px;
		background-color: $blue-3;
		color: $white;
		cursor: pointer;
		transition: background-color 0.3s;
		border-radius: 5px;

		@include tablet {
			padding: 15px 15px 15px 60px;
			font-size: 16px;
		}
	}

	&__icon {
		width: 20px;
		height: 20px;
		position: absolute;
		top: 50%;
		left: 10px;
		transform: translate(0, -50%);
		border-radius: 50%;
		border: 2px solid rgba($white, 0.5);
		transition: border-color 0.3s;

		@include tablet {
			width: 30px;
			height: 30px;
			border-width: 3px;
			left: 15px;
		}

		&:after {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			content: '\f00c';
			font-family: "Font Awesome 5 Free";
			font-weight: 600;
			font-style: normal;
			font-size: 10px;
			line-height: 1;
			opacity: 0;
			transition: opacity 0.3s;

			@include tablet {
				font-size: 13px;
			}
		}
	}
}

.checkbox-with-icon,
.radio-with-icon {
	display: flex;
	flex-direction: column;
	width: 100%;
	margin-bottom: 0;

	&:hover {
		.checkbox-with-icon__item,
		.radio-with-icon__item {
			background-color: darken($blue-3, 5%);
		}
	}

	&__input {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		padding: 0;
		border: 0;
		clip: rect(0 0 0 0);
		overflow: hidden;

		&:checked {
			+ .checkbox-with-icon__item,
			+ .radio-with-icon__item {
				background-color: darken($blue-3, 20%);
			}
		}

		&.error {
			+ .checkbox-with-icon__item,
			+ .radio-with-icon__item {
				background-color: $red;
			}
		}
	}

	&__item {
		padding: 10px;
		font-size: 14px;
		background-color: $blue-3;
		color: $white;
		cursor: pointer;
		transition: background-color 0.3s;
		border-radius: 5px;
		text-align: center;
		height: 100%;

		@include tablet {
			font-size: 16px;
		}

		@include desktops {
			padding: 20px;
			font-size: 18px;
		}
	}

	&__icon {
		width: 35px;
		height: 35px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 20px;
		margin: 0 auto 5px;
		line-height: 1;

		@include tablet {
			width: 50px;
			height: 50px;
			font-size: 30px;
			margin-bottom: 10px;
		}

		img {
			max-height: 100%;
			flex-shrink: 0;
		}
	}
}

.irs--flat {
	@media(max-width: 767px) {
		height: 50px;
	}

	.irs-bar {
		top: 30px;
		height: 10px;
		border-radius: 10px;
		background-color: $blue-3;

		@include tablet {
			top: 10px;
			height: 20px;
			border-radius: 20px;
		}
	}

	.irs-line {
		top: 30px;
		height: 10px;
		border-radius: 10px;
		background-color: #e6e6e6;

		@include tablet {
			top: 10px;
			height: 20px;
			border-radius: 20px;
		}
	}

	.irs-handle {
		width: 30px;
		height: 30px;
		background-color: #fbfbfb;
		box-shadow: 0 0 15px rgba($black, 0.15);
		border: 1px solid #cccccc;
		border-radius: 50%;
		top: 20px;

		i {
			display: none !important;
		}

		@include tablet {
			top: 0;
			display: none;
		}
	}

	.irs-single {
		color: $black;
		font-size: 16px;
		font-weight: 600;
		z-index: 1;
		line-height: 1.3;
		background-color: transparent;
		top: 0;

		@media(max-width: 767px) {
			left: 50% !important;
			transform: translate(-50%, 0);
		}

		@include tablet {
			padding: 7px 45px;
			font-size: 19px;
			background-color: #fbfbfb;
			border-radius: 40px;
			box-shadow: 0 0 15px rgba($black, 0.15);
			border: 1px solid #cccccc;
			cursor: pointer;
		}

		&:before,
		&:after {
			position: absolute;
			top: 50%;
			transform: translate(0, -50%);
			display: inline-block;
			font-style: normal;
			font-family: 'Font Awesome 5 Free';
			font-weight: 900;
			color: #cccccc;

			@media(max-width: 767px) {
				content: none !important;
			}
		}

		&:before {
			content: '\f053';
			left: 10px;
			border: none;
			bottom: auto;
			width: auto;
			height: auto;
			margin-left: 0;
		}

		&:after {
			content: '\f054';
			right: 10px;
		}
	}
}