.content-and-image-list {
	.content-and-image-item {
		&:not(:last-child) {
			margin-bottom: 60px;

			@include desktops {
				margin-bottom: 80px;
			}
		}

		&:nth-child(2n) {
			.row {
				@include tablet {
					flex-direction: row-reverse;
				}
			}

			.content-and-image-item__img-block {
				@include tablet {
					margin-right: 30px;
					margin-left: 0;
				}
			}
		}
	}
}

.content-and-image-item {
	&__img-block {
		margin-top: 30px;

		@include tablet {
			margin-left: 30px;
			margin-top: 0;
		}
	}
}