.-bg-shapes {
	@include desktops {
		background-image:
			url('../image/bg-shapes/shape-left.svg'),
			url('../image/bg-shapes/shape-right.svg');
		background-repeat: no-repeat;
		background-position: left 0 top 35%, right 0 bottom 35%;
		background-size: contain, contain;
	}
}

.-decoration-shadow {
	box-shadow: -3px 3px 20px rgba($black, 0.1);
}

.-section-bottom-divider {
	box-shadow: 0 5px 5px rgba($black, 0.025);
}