.newsletter-3 {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 55px;

	.form-group {
		label{
			margin-left: 20px;
			margin-bottom: 0;
			margin-top: 3px;
			i{
				font-weight: 700;
				color: $bali-gray;
			}
		}
		background: #fff;
		display: flex;
		align-items: center;
		width: 100%;
		height: 100%;
		margin-bottom: 0;
		border-radius: 5px;

		input {
			border: none;
			padding-left: 18px;
			color: $bali-gray;
		}
	}

	button {
