// contact page
.top-contact-info {
	.border-md-right {
		@include desktops {
			border-right: 1px solid #eceff4;
		}
	}
}

.contact {
	textarea {
		min-height: 222px;
	}
}

.text-placeholder-bali-gray {
	&::placeholder {
		color: $bali-gray;
	}
}

.mailchimp-subscribe-form {
	position: relative;
	max-width: 450px;

	input[type="text"] {
		@include placeholder {
			color: #b9b3b3;
		}

		display: block;
		width: 100%;
		background-color: $white;
		box-shadow: rgba($black, 0.2) 0px 2px 10px;
		border: 1px solid transparent;
		height: 64px;
		border-radius: 40px;
		padding: 15px 32px;
		font-weight: 500;
		font-size: 18px;
		color: $black;

		@include mobile {
			padding-right: 145px;
		}

		&:focus {
			@include placeholder {
				opacity: 0;
			}

			outline: none !important;
			border: 1px solid #666666;
		}
	}

	button[type="submit"] {
		font-size: 14px;
		color: $white;
		background-color: $black;
		border: none;
		font-weight: 700;
		border-radius: 40px;
		padding: 15px 40px;
		transition: background-color 0.3s;
		height: 64px;
		margin-top: 16px;

		@include mobile {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			margin-top: 0;
		}

		&:hover {
			background-color: rgba($black, 0.6);
		}
	}
}