body {
	min-width: 320px;
}

/* Border Color */
.border-golden-yellow {
		border-color: $golden-yellow !important;
}

.border-custom-white {
		border-color: rgba(236, 239, 244, 0.3) !important;
}

.timeline-area {
		.single-widgets {
				@include mobile-lg {
						width: 50%;
						flex: 0 0 50%;
				}

				@include desktops {
						width: 420px;
						flex: inherit;
				}

				&:nth-child(3) {
						@include desktops {
								margin-top: 75px;
						}
				}

				&:nth-child(4) {
						@include desktops {
								margin-top: 145px;
						}
				}
		}
}

.hover-pointer{
		&:hover{
				cursor: pointer;
		}
}

div#loading {
		width: 100%;
		height: 100vh;
		position: fixed;
		z-index: 9999999;
		background-color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;

		img{
				z-index: 9999;
		}
}

.min-font-size-55 {
	@media screen and (max-width: 575px) {
		font-size: 55px;
	}
}

.section-with-tabs {
	.nav-tabs {
		.nav-item {
			@media screen and (max-width: 991px) {
				margin: 10px 4px !important;
				display: flex;
			}
		}

		.nav-link {
			@media screen and (max-width: 991px) {
				font-size: 0;
				min-width: auto;
				padding: 0;
				border: 0;
				width: 33px;
				height: 4px !important;
				border-radius: 5px;
			}
		}
	}

	.btn-gray-1:not(:disabled):not(.disabled):active,
	.btn-gray-1:not(:disabled):not(.disabled).active,
	.show > .btn-gray-1.dropdown-toggle {
		color: $white;
		background-color: $blue-3;
		border-color: $blue-3;
	}

	.btn-gray-1:not(:disabled):not(.disabled):active:focus, .btn-gray-1:not(:disabled):not(.disabled).active:focus, .show > .btn-gray-1.dropdown-toggle:focus {
		box-shadow: 0 0 0 0.2rem rgba($blue-3, 0.5);
	}
}

.site-header {
	+ .main {
		padding-top: 56px;

		@include mobile-lg {
			padding-top: 71px;
		}
	}
}

.header {
	+ .main {
		padding-top: 80px;

		@include large-desktops {
			padding-top: 104px;
		}
	}
}

img {
	max-width: 100%;
	height: auto;
}

.video-image-link {
	&:after {
		content: '\f144';
		display: inline-block;
		font-family: 'Font Awesome 5 Free';
    font-weight: 900;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		font-size: 75px;
		opacity: 0;
		transition: opacity 0.5s;
		z-index: 5;
		color: $blue-3;
	}

	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($black, 0.2);
		z-index: 1;
		opacity: 0;
		transition: opacity 0.5s;
	}

	&:hover {
		&:before,
		&:after {
			opacity: 1;
		}
	}
}

.checkbox-block {
	display: block;
	background-color: #f5f8fe;
	border-radius: 5px;
	max-width: 600px;
	text-align: left;
	padding: 15px;
	margin-bottom: 0;
	cursor: pointer;

	&.-lg {
		@include mobile-lg {
			padding-left: 35px;
			padding-right: 35px;
		}

		&.check-input-control {
			.checkbox {
				min-width: 25px;
				max-width: 25px;
				min-height: 25px;
				max-height: 25px;

				&:after {
					font-size: 15px !important;
					line-height: 20px !important;
				}
			}
		}
	}

	p {
		font-size: inherit;
		color: inherit;
		margin-bottom: 7px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	.font-size-2 {
		line-height: 1.3;
	}
}

.bottom-video-text {
	span {
		min-width: 80px;
		text-align: left;
	}
}

.modal-image-link {
	position: relative;
	display: block;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		display: block;
		background-color: rgba($black, 0.2);
		opacity: 0;
		transition: opacity 0.3s;
	}

	&:hover {
		&:after {
			opacity: 1;
		}
	}
}

.modal {
	.modal-body {
		@media screen and (max-width: 991px) {
			padding: 70px 30px 30px;
		}

		@media screen and (max-width: 576px) {
			padding: 70px 15px 20px;
		}
	}

	.close {
		position: absolute;
		top: 20px;
		right: 20px;
		display: block;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: #d7dde7;
		opacity: 1;
		z-index: 5;
		cursor: pointer;
		transition: background-color 0.3s;

		@media screen and (max-width: 576px) {
			top: 10px;
			right: 10px;
		}

		&:hover {
			background-color: darken(#d7dde7, 5%);
			opacity: 1 !important;
		}

		span {
			display: inline-block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			line-height: 1;
			color: #888887;
			font-weight: 400;
			font-size: 30px;
			text-shadow: none;
		}
	}
}

.mailchimp-block {
	form {
		padding: 0 !important;
		margin-bottom: 40px;
	}

	.mc-field-group {
		width: 100% !important;
		padding-bottom: 10px !important;
		min-height: auto !important;
	}

	.button {
		margin: 0 !important;
	}

	input, button {
		&[type="email"] {
			border: 2px solid #c5c5c5 !important;
			font-size: 20px;
			padding: 10px 15px !important;
			text-indent: 0 !important;
			display: block;
			width: 100%;
			border-radius: 3px;
			line-height: 1;

			@media screen and (max-width: 991px) {
				font-size: 16px !important;
			}
		}

		&[type="submit"] {
			background-color: #1e1e1e !important;
			display: block !important;
			width: 100% !important;
			height: auto !important;
			border-radius: 0 !important;
			text-transform: uppercase;
			font-weight: 700 !important;
			font-size: 20px !important;
			letter-spacing: 1px;
			padding: 12px 15px !important;
			white-space: inherit !important;
			transition: background-color 0.3s;
			line-height: 1.2 !important;
			border: none;
			color: #ffffff;

			@media screen and (max-width: 991px) {
				font-size: 16px !important;
			}

			&:hover {
				background-color: lighten(#1e1e1e, 5%) !important;
			}
		}
	}

	div.mce_inline_error {
		padding: 0 !important;
		margin-bottom: 0 !important;
		color: #b52509 !important;
		display: block;
		width: 100%;
		text-align: center;
	}

	#mce-responses {
		text-align: center;

		.response {
			width: 100% !important;
			float: none !important;
			padding: 0 0 10px 0 !important;
			margin: 0 !important;
		}
	}

	.mc4wp-response {
		text-align: center;
	}
}

.row {
	&.float {
		display: block;
		overflow: hidden;

		.col,
		[class^="col-"] {
			float: left;
			width: 100%;
		}
	}
}

.container-small {
	max-width: 1068px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;
}

.container,
.container-fluid,
.container-sm,
.container-md,
.container-lg,
.container-xl,
.container-small {
	@media screen and (max-width: 479px) {
		padding-left: 24px;
		padding-right: 24px;
	}
}

.row {
	@media screen and (max-width: 479px) {
		margin-left: -24px;
		margin-right: -24px;
	}

	.col,
	[class^="col-"] {
		@media screen and (max-width: 479px) {
			padding-left: 24px;
			padding-right: 24px;
		}
	}
}

.text-dark-blue {
	color: #004796 !important;
}

.link-with-round-arrow {
	position: relative;
	display: inline-block;
	font-weight: 700;
	font-size: 16px;
	color: $black;
	line-height: 1.5;
	padding: 5px 58px 5px 0;

	&:after {
		content: '\ea02';
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
		right: 10px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: "Grayic";
		width: 32px;
		height: 32px;
		border-radius: 50%;
		background-color: $black;
		color: $white;
		font-size: 0.8em;
		line-height: 1;
		transition: background-color 0.3s, right 0.3s;
	}

	&:hover {
		color: $black;

		&:after {
			right: 0;
			background-color: $primary;
		}
	}
}

.review-item {
	&__img {
		display: block;
    border-radius: 40px;
		max-width: 390px;
		width: 100%;
		margin: 0 auto;

		@include tablet {
			margin-top: 32px;
		}
	}

	&__main-content {
		position: relative;
		margin-top: 30px;

		@include tablet {
			padding-left: 40px;
			margin-top: 0;
			height: 100%;
		}
	}

	&__bottom-items {
		@include mobile {
			padding-right: 130px;
		}
	}

	&__decoration-shape {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 90px;
		display: none !important;

		@include mobile {
			display: block !important;
		}

		@include tablet {
			width: 130px;
		}
	}
}

.slick-dotted {
	&.round-dots {
		.slick-dots {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
			padding-left: 0;
			margin-bottom: 0;
			padding-top: 30px;
			list-style-type: none;

			li {
				padding: 10px;
				display: inline-flex;
				line-height: 1;

				&.slick-active {
					button {
						background-color: $primary;
					}
				}
			}

			button {
				border: none;
				padding: 0;
				width: 12px;
				height: 12px;
				background-color: lightgrey;
				border-radius: 50%;
				font-size: 0;
				transition: background-color 0.3s;
			}
		}
	}
}

.pentagon-link {
	position: relative;
	display: inline-block;
	color: $black;
	font-weight: 800;
	font-size: 16px;
	height: 136px;
	width: 136px;

	@include desktops {
		height: 192px;
		width: 192px;
		font-size: 24px;
	}

	&:hover {
		color: $white;

		.pentagon-link__shape {
			transform: rotate(15deg);
			fill: $primary;
		}
	}

	&__content {
		position: relative;
		z-index: 5;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}

	&__shape {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 1;
		width: 100%;
		fill: #18e884;
		transition: all 0.2s ease 0s;
	}

	&__text {
		padding-bottom: 15px;
	}

	&__arrow-icon {
		font-weight: 900;
		font-size: 1.3em;
	}
}