.card {
	&-step {
		position: relative;
		padding-left: 12px;
		max-width: 270px;
		margin-bottom: 70px;

		@include tablet {
			margin-bottom: 30px;
		}

		&__content {
			border-left: 1px solid #f2f2f2;
			padding-left: 28px;
		}

		&__number {
			position: absolute;
			top: 0;
			left: 0;
			width: 24px;
			height: 24px;
			border-radius: 50%;
			background-color: $primary;

			&:before {
				counter-increment: card-step;
				content: counter(card-step) '.';
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				color: $white;
				font-size: 12px;
				font-weight: 900;
			}
		}

		&__title {
			max-width: 150px;
		}

		&__description {
			max-width: 230px;

			@include mobile-lg {
				font-size: 18px;
			}
		}
	}

	&-img-title-and-text {
		margin-bottom: 55px;

		&__img-block {
			@include responsive-ratio(504, 473);

			position: relative;
			margin-bottom: 40px;
		}

		&__img {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: center;
		}
	}
}